@import url(https:////at.alicdn.com/t/font_2559940_lresuup7y1.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  background-image: linear-gradient(50deg, #dfe9f3 0%, white 100%);
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
::-webkit-scrollbar {
  /*滚动条整体样式*/
  width: 6px !important;
  /*高宽分别对应横竖滚动条的尺寸*/
  height: 6px !important;
  background: #ffffff !important;
  cursor: pointer;
}

::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  border-radius: 5px !important;
  box-shadow: inset 0 0 5px rgba(240, 240, 240, 0.5) !important;
  background: rgba(63, 98, 131, 0.8) !important;
  cursor: pointer !important;
}

::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  box-shadow: inset 0 0 5px rgba(240, 240, 240, 0.5) !important;
  border-radius: 0 !important;
  background: rgba(240, 240, 240, 0.5) !important;
  cursor: pointer !important;
}

/*placeholder字体设置*/ 
::-webkit-input-placeholder { /* WebKit browsers */
  color: #999;
  font-size: 16px;
}

::-moz-placeholder { /* Mozilla Firefox 19+ */
  color: #999;
  font-size: 16px;
}

:-ms-input-placeholder { /* Internet Explorer 10+ */
  color: #999;
  font-size: 16px;
} 

.App {
    width: 500px;
    height: auto;
    margin: 12vh auto;
    position: relative;
}
.App .sam-todos {
    background-color: #58C9B9;
    box-shadow: 0px 2px 5px 4px #9DC8C8;
    border-radius: 5px;
}

.App .sam-header h1 {
    margin: 0px;
    font-size: 40px;
}
.App .sam-header {
    text-align: center;
    height: 10vh;
}

.App .randomTheme-btn {
    position: absolute;
    top: -10px;
    left: 0px;
    border: none;
    outline: none;
    padding: 10px;
    background-color: transparent;
}
.App .randomTheme-btn i {
    font-size: 20px;
    cursor: pointer;
    color: #519D9E;
}

.App .randomTheme-btn::before {
    content: attr(data-tip);
    width: 70px;
    padding: 10px 5px;
    /* white-space: nowrap; */
    position: absolute;
    background-color: #8CD790;
    color: white;
    border-radius: 5px;
    top: -1px;
    opacity: 0;
    transition: all 0.5s ease;
    left: 56px;
}
.App .randomTheme-btn::after {
    content: "";
    border: 7px solid transparent;
    border-right: 7px solid #8CD790;
    position: absolute;
    left: 42px;
    top: 5px;
    transform: rotateX(1.5deg);
    opacity: 0;
    transition: all 0.5s ease;
}
.App .randomTheme-btn:hover::before {
    opacity: 1;
    left: 60.5px;
}

.App .randomTheme-btn:hover::after {
    opacity: 1;
    left: 46px;
}

.todos-header {
    position: relative;

}

.todos-header .todos-input {
    display: inline-block;
    height: 50px;
    padding: 5px 5px 5px 10px;
    border: none;
    outline: none;
    width: 510px;
    transform: translate(-5px,-1px);
    box-sizing: border-box;
    font-size: 20px;
    color: #3f4040;
    border-radius: 10px;
}

.todos-header .todos-input-btn {
    position: absolute;
    right: -5px;
    top: -1px;
    height: 50px;
    width: 70px;
    border-radius: 0px 10px 10px 0px;
    border: none;
    outline: none;
    cursor: pointer;
}
.sam-todos-list {
    /* overflow: hidden; */
}
.todos-list {
    list-style: none;
    /* padding: 10px; */
    color: white;
    /* width: 500px; */
    max-height: 500px;
    /* white-space: nowrap; */
    -webkit-overflow-scrolling: touch;
    overflow-x: hidden;
    overflow-y: auto;
    padding: 10px;
    margin-bottom: -.2rem;
    overflow: -moz-scrollbars-none;
    overflow: -moz-scrollbars-none;
}
.todos-list li {
    padding: 10px;
    cursor: pointer;
    position: relative;
    /* padding: 10px; */
}

.todos-list li i {
    cursor: pointer;
    font-size: 16px;
    margin-right: 10px;
    fill: currentColor;
}
.todos-list li .btn-controls {
    position: absolute;
    right: 0px;
    top: 10px;
}
.todos-list li .btn-controls i {
    margin-right: 10px;
}
.todos-list li .btn-controls i:hover {
    color: #5c196b;
}

.todos-slide {
    position: absolute;
    top: 50px;
    right: -75px;
    width: 45px;
    /* overflow: hidden; */
}

.todos-slide .btn {
    width: 45px;
    height: 45px;
    border-radius: 45px;
    margin: 10px 0px;
    cursor: pointer;
    border: none;
    background-color: #D7FFF1; 
    transition: all .5s ease;
    position: relative;
}
.todos-slide .btn i.iconfont {
    color: #519D9E;
}
.todos-slide .btn:hover {
    background-color: #285943;
}
.todos-slide .btn i.iconfont:hover {
    color: #D7FFF1;
}

/* button-hover显示提示文字 */
.todos-slide button.btn::before {
    content: attr(data-tip);
    width: 70px;
    padding: 10px 5px;
    /* white-space: nowrap; */
    position: absolute;
    background-color: #8CD790;
    color: white;
    border-radius: 5px;
    top: -1px;
    opacity: 0;
    transition: all 0.5s ease;
    left: 56px;
}
.todos-slide button.btn::after {
    content: "";
    border: 7px solid transparent;
    border-right: 7px solid #8CD790;
    position: absolute;
    left: 42px;
    top: 5px;
    transform: rotateX(1deg);
    opacity: 0;
    transition: all 0.5s ease;
}
.todos-slide button.btn:hover::before {
    opacity: 1;
    left: 60px;
}
.todos-slide button.btn:hover::after {
    opacity: 1;
    left: 46px;
}
.todos-popup {
    position: fixed;
    top: 20vh;
    left: 50%;
    transform: translateX(-50%);
    width: 400px;
    max-height: 280px;
    background-color: #58c9b9;
    box-sizing: border-box;
    border-radius: 20px;
    box-shadow: 0px 0px 3px 1px #D7FFF1;
    /* overflow: hidden; */
}

.todos-popup .popup-header {
    background-color: #3b8686;
    color: white;
    padding-left: 20px;
    border-radius: 20px 20px 0px 0px;
    display: flex;
    justify-content: space-between;
}
.todos-popup button {
    outline: none;
    cursor: pointer;
    border: none;
}
.todos-popup .popup-header button {
    border-radius: 50%;
    height: 40px;
    width: 40px;
    margin-top: 10px;
    margin-right: 10px;
    background-color: transparent;
    color: D7FFF1;
}

.todos-popup .popup-header button i{
    transition: all .5s ease-in;
    color: #D7FFF1;
}
.todos-popup .popup-header button i:hover {
    color: #58c9b9;
    font-size: 20px;
}

.todos-popup .popup-body{
    /* width: 400px; */
    overflow: hidden;
    /* background-color: black; */
}
.todos-popup .popup-body .popup-content {
    overflow-y: scroll;
    padding: 5px 10px 2px;
    font-size: 18px;
    color: white;
    max-height: 200px;
    min-height: 150px;
    width: 406px;
    margin-bottom: 10px;
    box-sizing: border-box;
    /* background-color: blue; */
}

.todos-popup .popup-footer{
    /* height: 50px; */
    overflow: hidden;
    transform: translateY(-10px);
}

.todos-popup .popup-footer button{
    margin-right: 10px;
    float: right;
    padding: 10px;
    width: 50px;
    border-radius: 10%;
    background-color: transparent;
}
